.dark{
  background-color: rgb(82, 84, 87);
  // background:radial-gradient(ellipse at bottom, #7b7f8a 40%, #000000 100%);
  --primary-text-color: #f8f1f1;
  --title-text-color: #FF6B08;
  --secondary-title-color: #ffffff;
  --footer-text-color: #dcdceb;
  --background-footer: #252525;
  --background-body: #ffffff;
  color: #f7f7f7;
  transition: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
body {
   transition: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.theme-btn {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition: 0.4s;
  position: absolute;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 10px;
  border-radius: 15px;
  transition: 0.3s;
  &:hover {
    transform:  rotate(-20deg) scale(1.25);
    -webkit-transform: rotate(-20deg) scale(1.15);
}
 @media screen and (min-width: 320px) {
      font-size: 20px;
      top: 12vh;
      right: 50%;
      transform: translateX(120px);
    }
    @media screen and (min-width: 768px) {
      font-size: 30px;
       top: 10vh;
      right: 50%;
      transform: translateX(350px);
    }
    @media screen and (min-width: 1024px) {
      top: 10vh;
      right: 50%;
      transform: translateX(470px);
    }
    
}
